@charset "UTF-8";

/**
 * Основные стили
 **/

html {
	font-size: 16px;
	color: $black;
	height: 100%;
}
body {
	font-family: 'Arial', sans-serif;
	overflow-wrap: break-word;
	word-wrap: break-word;
	height: 100%;
}

.container {
	@include min-max(320px, 100%, 0);
	height: 100%;
}
.wrapper {
	@include min-max(320px, 1200px, 45px);;
}

@media (max-width: 576px) {
	.container {
		padding: 0;
	}
}

