$bold: 700;
$TitleColor: #a1a3a5;
$SubTitleColor: #2f2f2f;
$TextColor: #222428;
$LinkColor: #236aa6;

h2 {
  color: $TitleColor;
  font-family: 'Abhaya Libre', serif;
  font-size: 37px;
  line-height: 56px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
}

h3 {
  color: $SubTitleColor;
  font-family: 'Abhaya Libre', serif;
  font-size: 60px;
  letter-spacing: 1px;
  line-height: 56px;
  text-align: center;
}

p {
  color: $TextColor;
  font-size: 15px;
  font-weight: 400;
}

a {
  text-transform: uppercase;
  color: $LinkColor;
  text-decoration: none;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
}


